.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

div{
  box-sizing: border-box;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.color-purple{
  color:#C15AF4;
}

.color-orange{
  color: #FF8200;
}

.color-pink{
  color:#FF7E9D;
}

#main{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#main-intro{
  display:flex;
  width: 800px;
  padding-left: 50px;
  padding-right: 50px;
  /* border: 1px solid black; */
  transition: all 0.5s;
}

.up{
  transform: translate(0px, -50px);
}

#main-profile{
  width: 135px;
  height: 135px;
  border-radius: 50%;
  margin-left: 15%;
  transition: all 0.5s;
}


.turn360{
  transform:rotateY(360deg);
}

#main-text{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 50px;
}

#main-title{
  font-size: 50px;
  font-weight: 900;
  color:#6496FF;
}

#main-subtitle{
  font-size: 15px;
  text-align: center;
  font-weight: 300;
  color:#9BC3FF	;
}

#main-category{
  display: flex;
}

#main-category{
  transition: all 0.5s;
}
.category{
  margin-left: 30px;
  text-decoration: underline;
  cursor: pointer;
}

.fadeOut{
  opacity: 0;
}
.fadeIn{
 opacity: 1;
}
